<template>
  <div class="main-container">
    <div class="main-wrap">
      <header>
        <div class="logo-wrap">
          <img src="@/assets/lush_logo.png">
        </div>
      </header>
      <section class="login-input-section-wrap">
        <div class="login-input-wrap">
          <div class="">
            <div>
              대기 순번 <span v-bind:id="waitingNumber"></span>
            </div>
            <div>
              등록 일시 : {{ createDt }}
            </div>
          </div>

          <div>
            <span>현재 대기 인원 {{ waitingCount }} 명</span>
          </div>

        </div>

        <div class="login-button-wrap" style="width: 465px !important;">
          <button type="button" class="btn-cancel" @click="cancel">대기 취소</button>
          <button type="button" class="btn-refresh" @click="refresh">새로 고침</button>
        </div>

        <div class="login-input-wrap password-wrap">
          <h3>[대기중] 고객님의 순서가 되면 알림톡(문자)로 알려드립니다.</h3>
        </div>
      </section>

      <footer>
        <div class="copyright-wrap">
          <span>Copyright © Lush Korea Corp. All Rights Reserved.</span>
        </div>
      </footer>
    </div>
  </div>
</template>
<script>

import * as confirmApi from "@/api/confirm";
import * as cancelApi from "@/api/cancel";

export default {
  data() {
    return {
      storeSeq: '',
      result: '',
      resultCode: '',
      waitingNumber: '',
      waitingCount: '',
      createDt: '',
      shopName: '',
      openTime: '',
      closeTime: '',
      rtnUrl: '',
      shopCode: '',
      uid: '',
    }
  },
  methods: {
    cancel() {
      cancelApi.cancel_user(this.storeSeq, this.shopCode, this.uid)
          .then(res => () => {
            console.log(res.data.result);
            alert("정상적으로 취소 처리 되었습니다.");
          });
    },
    refresh() {
      this.$router.go();
    }
  },
  mounted() {
    const data = {
      uid: this.$route.params.uid,
      shopCode: this.$route.params.shopCode,
      storeSeq: '',
      rtnUrl: '',
    }

    const jData = JSON.stringify(data);
    const blob = new Blob([jData], {type: "application/json"})

    console.log(data);
    console.log(jData);
    console.log(blob);

    // 화면 접속시 상세 정보 호출
    this.axios.post("/user/check_waiting/" + this.$route.params.shopCode + "/" + this.$route.params.uid)
        .then(res => () => {
          this.storeSeq = res.data.storeSeq;
          this.storeSeq = res.data.resultCode;
          this.uid = res.data.uid;
          this.rtnUrl = res.data.rtnUrl;
        })
        .catch(error => {
          console.log(error)
        });

    // 호출된 정보로 3초 후 URL 찌름
    setTimeout(() => {
      confirmApi.confirm_user(data)
          .then(res => console.log(res.data))
          .catch(error => {
            console.log(error)
          })
    }, 3000)
  }
}
</script>