import { createWebHistory, createRouter } from "vue-router";
import Board from "@/views/Board";
import Confirm from "@/views/Confirm";
import Main from "@/views/Main";

const routes = [
    {
        // 메인화면
        path: '/user/add_waiting/:shopCode',
        name: 'waitingUser',
        component: Main
    },
    {
        // 대기 등록 화면
        path: '/user/check_waiting/:shopCode/:uid',
        name: 'confirmUser',
        component: Confirm
    },
    {
        path: '/user/user_waiting/:shopCode/:uid',
        name: 'checkUser',
        component: Board
    },
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

export default router
