<script>
import * as submitApi from "@/api/submit";
import dayjs from 'dayjs'

export default {
  data() {
    return {
      memTel: '',
      shopCode: '',
      storeSeq: ''
    }
  },
  methods: {
    reservation() {
      if (this.memTel === "") {
        alert("데이터가 없습니다.");
        return false;
      }

      if (!document.querySelector("input[name=approve]").checked) {
        alert("약관을 동의하여 주세요.");
        return false;
      }

      const data = {
        memTel: this.memTel,
        shopCode: this.$route.params.shopCode,
        storeSeq: this.$route.params.shopCode + "_" + dayjs().format("YYYYMMDDHHmmss") + "_" + Math.ceil(Math.random()*10000000000000)
      };

      const jData = JSON.stringify(data);
      const blob = new Blob([jData], {type: "application/json"})

      console.log(data);
      console.log(jData);
      console.log(blob);

      // this.axios.post("http://34.64.200.143:80/ring/user/add_waiting.do", blob).then(res => console.log(res.data)).catch(error => console.log(error));

      submitApi.add_waiting(jData)
          .then(res => console.log(res.data))
          .catch(error => {console.log(error)})
    }
  },

}
